import {
  UnorderedList,
  ListItem,
  Wrap,
  WrapItem,
  Box,
  Heading,
  Image,
  Stack,
  Badge,
  VStack,
} from "@chakra-ui/react";
const data = require("../data/TechData.json");

export default function TechComp() {
  return (
    <>
      <VStack spacing="3vh">
        <Heading size="xl" color="white">
          My Skills and Expertise
        </Heading>
        <Wrap justify="center" spacing="5vh">
          {data.map((ele, ind) => (
            <WrapItem>
              <Box
                bg={ele.color}
                p={4}
                maxWidth="30vw"
                borderRadius="lg"
                overflow="hidden"
              >
                <Heading p={2} color="white" font="lg" orientation="horizontal">
                  {ele.name}
                </Heading>
                <UnorderedList color="white">
                  {ele.ls.map((ind) => (
                    <Stack direction="row">
                      <ListItem>{ind[0]}</ListItem>
                      <Badge
                        ml="1"
                        variant="subtle"
                        colorScheme="green"
                        h="3vh"
                      >
                        {ind[1]}
                      </Badge>
                    </Stack>
                  ))}
                </UnorderedList>
                <Wrap spacing="2vh" justify="center">
                  {ele.img.map((ind) => (
                    <WrapItem>
                      <Image
                        boxSize="15vh"
                        src={process.env.PUBLIC_URL + ind}
                        alt={ind.split("/")[-1]}
                        p={2}
                      />
                    </WrapItem>
                  ))}
                </Wrap>
              </Box>
            </WrapItem>
          ))}
        </Wrap>
      </VStack>
    </>
  );
}
