import Particles from "react-particles-js";
import { Box } from "@chakra-ui/react";

export default function ParComp() {
  return (
    <Box position="fixed" t="0" l="0" zIndex="-1">
      <Particles
        params={{
          particles: {
            number: {
              value: 80,
            },
            size: {
              value: 3,
            },
          },
        }}
        width="100vw"
        height="100vh"
      />
    </Box>
  );
}
