import {
  Accordion,
  AccordionButton,
  AccordionIcon,
  AccordionItem,
  AccordionPanel,
  Box,
  Text,
  Wrap,
  WrapItem,
  VStack,
  Badge,
  Link,
} from "@chakra-ui/react";
const data = require("../data/ExpData.json");

export default function ExpComp() {
  return (
    <Box bg="teal" width="95vw">
      <Accordion allowToggle color="white">
        <Wrap spacing="2vw" justify="center" p={4}>
          {data.map((ele) => (
            <WrapItem>
              <AccordionItem
                width="25vw"
                borderColor="white"
                borderWidth="2px"
                borderRadius="md"
                p={1}
              >
                <AccordionButton>
                  <Text flex="1" textAlign="left" fontSize="md">
                    <b>{ele.name}</b>
                  </Text>
                  <AccordionIcon />
                </AccordionButton>
                <AccordionPanel>
                  <VStack>
                    <Text>{ele.desc}</Text>
                    <Wrap spacing="1vh">
                      {ele.lib.map((e) => (
                        <WrapItem>
                          <Badge colorScheme="purple">{e}</Badge>
                        </WrapItem>
                      ))}
                    </Wrap>
                    <Link href={ele.link} isExternal>
                      View Project
                    </Link>
                  </VStack>
                </AccordionPanel>
              </AccordionItem>
            </WrapItem>
          ))}
        </Wrap>
      </Accordion>
    </Box>
  );
}
