import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalCloseButton,
  ModalBody,
  Link,
  Button,
  VStack,
} from "@chakra-ui/react";
import { AiFillLinkedin } from "react-icons/ai";
import { SiGmail } from "react-icons/si";

export default function ContComp(props) {
  return (
    <Modal onClose={props.onClose} isOpen={props.isOpen} isCentered>
      <ModalOverlay />
      <ModalContent>
        <ModalHeader>Contact Me!!</ModalHeader>
        <ModalCloseButton />
        <ModalBody>
          <VStack spacing="5vh">
            <Button
              leftIcon={<AiFillLinkedin />}
              colorScheme="teal"
              variant="link"
              size="lg"
            >
              <Link
                href="https://www.linkedin.com/in/p-r-hebbar"
                isExternal={true}
              >
                Linkedin
              </Link>
            </Button>
            <Button
              leftIcon={<SiGmail />}
              colorScheme="teal"
              variant="link"
              size="lg"
            >
              <Link href="mailto: r.hebbar1998@gmail.com" isExternal={true}>
                My Email
              </Link>
            </Button>
          </VStack>
        </ModalBody>
      </ModalContent>
    </Modal>
  );
}
