import { Avatar, VStack, Text, HStack, Icon, Link } from "@chakra-ui/react";
import { AiFillGithub, AiFillLinkedin } from "react-icons/ai";

export default function HomeComp() {
  return (
    <VStack spacing={8}>
      <Avatar
        size="2xl"
        name="prof"
        src={process.env.PUBLIC_URL + "/assets/Photo.jpg"}
      />
      <Text color="white" fontSize="4xl">
        I'm Rahul
      </Text>
      <Text color="white" fontSize="3xl">
        B.Tech Graduate from IIIT Trichy - ECE
      </Text>
      <HStack spacing={5}>
        <Link href="https://github.com/rahul-hebbar" isExternal>
          <Icon as={AiFillGithub} boxSize="2em" color="white" />
        </Link>
        <Link href="https://www.linkedin.com/in/p-r-hebbar" isExternal>
          <Icon as={AiFillLinkedin} boxSize="2em" color="white" />
        </Link>
      </HStack>
    </VStack>
  );
}
