import {
  WrapItem,
  VStack,
  Wrap,
  Text,
  Box,
  Heading,
  HStack,
  Container,
  Avatar,
  Flex,
  Button,
  useDisclosure,
  ModalBody,
  Modal,
  ModalOverlay,
  Image,
  ModalContent,
} from "@chakra-ui/react";
import { useState } from "react";
const data = require("../data/AchiData.json");

export default function AchComp() {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [butid, setButid] = useState(0);

  function butAction(event) {
    setButid(event.target.id);
    onOpen();
  }

  return (
    <>
      <VStack spacing="3vh">
        <Wrap justify="center" spacing="5vh">
          {data.map((ele, ind) => (
            <WrapItem>
              <Box
                bg="teal.500"
                p={4}
                maxWidth="30vw"
                borderRadius="lg"
                overflow="hidden"
              >
                <Heading size="md" color="white" paddingBottom={2}>
                  {ele.name}
                </Heading>
                <HStack spacing="2vh">
                  <Container>
                    <Text size="sm" color="gold">
                      <b>Year:</b> {ele.year}
                    </Text>
                    <Text size="sm" color="white">
                      <b>Position:</b> {ele.pos}
                    </Text>
                    <Text size="sm" color="gold">
                      <b>Project Category:</b> {ele.projcat}
                    </Text>
                    <Text size="sm" color="white">
                      <b>Project:</b> {ele.proj}
                    </Text>
                  </Container>
                  <Avatar size="xl" src={process.env.PUBLIC_URL + ele.logo} />
                </HStack>
                <Flex flexDirection="row-reverse">
                  <Button
                    colorScheme="teal"
                    variant="solid"
                    id={ind}
                    onClick={butAction}
                  >
                    View Certificate
                  </Button>
                </Flex>
              </Box>
            </WrapItem>
          ))}
        </Wrap>
        <Text size="xs" color="white">
          Project links can be found in Experience page
        </Text>
      </VStack>
      <Modal isOpen={isOpen} onClose={onClose} size="2xl">
        <ModalOverlay />
        <ModalContent>
          <ModalBody>
            <Image
              src={process.env.PUBLIC_URL + data[butid].img}
              onContextMenu={(e) => e.preventDefault()}
            />
          </ModalBody>
        </ModalContent>
      </Modal>
    </>
  );
}
