import { Image } from "@chakra-ui/react";

export default function BgImg() {
  return (
    <>
      <Image
        src={process.env.PUBLIC_URL + "/assets/bg.jpg"}
        alt="bg"
        zIndex="-2"
        width="100vw"
        height="100vh"
        position="fixed"
        filter="brightness(40%)"
      />
    </>
  );
}
