import {
  Button,
  Flex,
  HStack,
  Link,
  Text,
  useDisclosure,
} from "@chakra-ui/react";
import { Link as Wlink, useRoute } from "wouter";
import ContComp from "./ContComp";

export default function NavComp() {
  const { isOpen, onOpen, onClose } = useDisclosure();

  const [hmatch] = useRoute("/home");
  const [tmatch] = useRoute("/tech");
  const [amatch] = useRoute("/achi");
  const [ematch] = useRoute("/exp");

  return (
    <>
      <Flex flexDir="row-reverse" h="5vh">
        <HStack spacing="5vh">
          <Link as={Wlink} href="/home">
            <Text
              fontSize="2xl"
              color={hmatch ? "cyan" : "white"}
              cursor="pointer"
            >
              Home
            </Text>
          </Link>
          <Link as={Wlink} href="/exp">
            <Text
              fontSize="2xl"
              color={ematch ? "cyan" : "white"}
              cursor="pointer"
            >
              Experience
            </Text>
          </Link>
          <Link as={Wlink} href="/achi">
            <Text
              fontSize="2xl"
              color={amatch ? "cyan" : "white"}
              cursor="pointer"
            >
              Achievements
            </Text>
          </Link>
          <Link as={Wlink} href="/tech">
            <Text
              fontSize="2xl"
              color={tmatch ? "cyan" : "white"}
              cursor="pointer"
            >
              Technical Skills
            </Text>
          </Link>
          <Button
            fontSize="2xl"
            colorScheme="teal"
            variant="solid"
            _hover={{ bgColor: "orange.500" }}
            onClick={onOpen}
          >
            Contact
          </Button>
          <ContComp onClose={onClose} isOpen={isOpen} />
        </HStack>
      </Flex>
    </>
  );
}
